import { Component, OnInit } from '@angular/core';
import { UsuarioService } from '../../cadastro-usuario/shared/usuario.service';
import { Usuario } from 'src/app/interfaces/usuario';

@Component({
  selector: 'app-dados-conta',
  templateUrl: './dados-conta.component.html',
  styleUrls: ['./dados-conta.component.css']
})
export class DadosContaComponent implements OnInit {
  usuario: Usuario = <Usuario>{};
  constructor(private usuarioService: UsuarioService) { }

  ngOnInit() {
    this.usuarioService.getDadosUsuario().subscribe(
      (data: Usuario) => {
        this.usuario = data;
        console.log(data);
      }
    )
  }

}
